const $ = function (el) {
    if (Array.isArray(el)) {
        return document.querySelectorAll(el);
    }

    return document.querySelector(el);

}

const preview = $([".products__item-preview"]);



preview.forEach((preview) => {

    let previewInputs = preview.querySelectorAll("input");



    previewInputs.forEach((input) => {

        

            input.addEventListener("click", () => {

                const label = input.nextElementSibling;

                const previewImg = label.querySelector("img");

                const mainImg = preview.parentNode.querySelector(

                    ".products__item-pic"

                );



                mainImg.setAttribute("src", previewImg.getAttribute('src'));

            });



    });

});



